import * as React from 'react'
import { Link, graphql } from 'gatsby'
import Layout from '../../components/layout'
import Seo from '../../components/seo'
import './index.css'
import formatDate from '../../utils/formatDate'

const LiteraturePage = ({ data }) => {
  data.allMdx.nodes.map(node => {console.log(node.frontmatter)});
  return (
    <Layout pageTitle="Literatures Posts">
      {
        data.allMdx.nodes.map(node => (
          <article key={node.id}>
            <h3>
              <Link className='entry-title' to={`/literatures/${node.frontmatter.slug}`}>
                {node.frontmatter.title}
              </Link>
            </h3>
            { !true ?null:
            <><p className='entry-date'>{formatDate(node.frontmatter.date)}</p>
            <p >{node.excerpt}</p></>
            }
          </article>
        ))
      }
    </Layout>
  )
}

export const query = graphql`
  query {
    allMdx(sort: { frontmatter: { date: DESC }}
        filter: { frontmatter: { category: { eq: "literature" } } }
        ) {
      nodes {
        frontmatter {
          date
          title
          slug
        }
        id
        excerpt
      }
    }
  }
`

export const Head = () => <Seo title="Blog Posts" />

export default LiteraturePage